___CSS_LOADER_EXPORT___.push([module.id, `.slider {
  position: relative;
  width: 800px;
  height: 600px;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 20px;
}

.slider-container {
  position: relative;
  display: flex;
  flex-grow: 0;
  width: 100%;
  height: 100%;
  transition: transform 0.5s ease-in-out;
}

.slider-item {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  overflow: hidden;
}

.slider-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 2s ease-in-out;
}

.slider-item--active img {
  transform: scale(1.1);
}

.slider-controls {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  justify-content: space-between;
  width: 100%;
  z-index: 1;
}

.slider-controls button {
  width: 60px;
  height: 60px;
  padding: 0;
  background: none;
  border: none;
  outline: none;
  color: #ffffff;
  cursor: pointer;

  &:hover {
    background-color: rgb(255 255 255 / 0.15);
  }

  &:active {
    background-color: rgb(255 255 255 / 0.2);
  }
}

.slider-controls svg {
  width: 100%;
  height: 100%;
}

.slider-indicators {
  position: absolute;
  bottom: 25px;
  left: 50%;
  display: inline-flex;
  column-gap: 10px;
  z-index: 1;
  transform: translateY(-50%);
}

.indicator {
  width: 15px;
  height: 15px;
  padding: 0;
  background-color: rgb(255 255 255 / 0.4);
  border: 1px solid #302f2f;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.3s ease;

  &:hover:not(:disabled) {
    background-color: rgb(255 255 255 / 0.55);
  }

  &:disabled {
    cursor: auto;
  }
}

.indicator--active {
  background-color: #ffffff;
}
